.buttons {
  display: flex;
}
.buttons .btn {
  display: block;
  position: relative;
  max-width: 100%;
  padding: 15px 45px;
  background-color: rgba(0, 0, 0, 0);
  color: #ff9600;
  transition: all 300ms ease-in-out;
  z-index: 0;
}
.buttons .btn:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  z-index: -1;
  background-color: #ff9600;
  transition: height 300ms ease-in-out, background-color 150ms ease-in-out 150ms;
}
.buttons .btn:hover {
  color: #ffffff;
}
.buttons .btn:hover:before {
  height: 100%;
  background-color: #005290;
  transition: height 300ms ease-in-out, background-color 150ms ease-in-out;
}
.buttons .btn + .btn {
  margin-left: 20px;
}

.btn {
  text-align: center;
  text-decoration: none;
  font-family: "TradeGothic", Helvetica, Arial, sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1.1;
  font-size: 24px;
  transition: all 300ms ease-in-out;
}

.btn-white, .layout.cta.blue .btn {
  color: #ffffff;
}
.btn-white:before, .layout.cta.blue .btn:before {
  background-color: #ffffff;
}
.btn-white:hover, .layout.cta.blue .btn:hover {
  color: #ffffff;
}
.btn-white:hover:before, .layout.cta.blue .btn:hover:before {
  background-color: rgba(59, 59, 59, 0.75);
}

